import {
    Flex, Image, Badge, Text, Spacer, Center, Box,
} from '@chakra-ui/react'
import { CommerceContext } from '../../../App'
import { useContext } from 'react'
import { Link } from 'react-router-dom';

export default function Item({ item }) {
    const context = useContext(CommerceContext);
    return (
        <Flex
            w='100%'
            borderWidth="1px"
            borderRadius="lg"
            key={item.id}
            p='2'
            my='5'>
            <Image
                src={item.media.source}
                maxW={['100px', '130px']}
                maxH={['100px', '100%']}
                mr='5'
                borderRadius='lg'
                loading='lazy'
            />
            <Flex
                w='100%'
                overflow='hidden'
                flexWrap='wrap'>
                <Flex
                    pos='relative'
                    flexDir='column'
                    w='100%'
                    justifyContent='space-between'
                    fontSize={['sm', 'lg']}
                    fontWeight='bold'>
                    
                    <Flex flexDir='column' h='100%'>
                        <Text 
                            as={Link}
                            to={`/przedmiot/${item.permalink}`}
                            isTruncated
                            maxW='100%'>{item.name}</Text>
                        
                        <Badge w='max-content' colorScheme={context.colorScheme}>{ item.price.formatted_with_code }</Badge>
                        
                        {item.selected_options.length > 0 && item.selected_options.map(option => (
                            <Text fontWeight='normal' fontSize='md' key={option.group_id}>
                                {option.group_name === 'size' && 'Rozmiar: '}
                                {option.group_name === 'color' && 'Kolor: '}
                                {item.selected_options.map(opt => opt.option_name)}
                            </Text>
                        ))}
                        
                        <Spacer />
                        <Box color='green'>
                            {item.quantity * item.price.raw} PLN
                        </Box>
                    </Flex>

                    <Flex
                        pos='absolute'
                        right='2'
                        top='50%'
                        transform='translateY(-50%)'
                        bgColor='white'
                        flexDir='column'
                        borderRadius='xl'
                        borderWidth='1px'
                        boxShadow='sm'
                        overflow='hidden'>
                        <Center
                            cursor='pointer'
                            _active={{
                                transform: 'scale(0.98)',
                            }}
                            onClick={() => { context.handleUpdateItem(item.id, item.quantity+1)}}
                        >+</Center>
                        
                        <Center
                            px='2'
                            flex='1'
                            borderRadius='lg'
                            fontSize={['sm', 'lg']}>
                            {item.quantity}
                        </Center>
                        
                        <Center
                            cursor='pointer'
                            _active={{
                                transform: 'scale(0.98)',
                            }}
                            onClick={() => { context.handleUpdateItem(item.id, item.quantity - 1)}}
                        >-</Center>
                    </Flex>
                </Flex>
                <Spacer />
                
            </Flex>
        </Flex>
    )
}
