import { Badge, Box, Circle, Flex, Spacer, Text } from '@chakra-ui/react';
import { Icon } from '@iconify/react';
import { useContext } from 'react';
import { Link } from 'react-router-dom';
import { CommerceContext } from '../../App';

export default function Navbar({ cart }) {
    const context = useContext(CommerceContext);
    
    if (!cart) return 'adowanie...';

    return (
        <Flex
            backgroundColor={context.colorScheme + `.50`}
            p='5'
            pos='sticky'
            top='0'
            zIndex='1'
            boxShadow='md'
        >
            <Box as={Link} to='/' fontSize='md' fontWeight='bold'>cmmrc</Box>
            <Spacer />
            <Flex
                as={Link}
                to='/cart'
                pos='relative'
                justifyContent='center'
                alignItems='center'
                cursor='pointer'>
                <Text fontSize='lg'><Icon icon="ant-design:shopping-cart-outlined" /></Text>
                <Circle
                    pos='absolute'
                    left='3'
                    top='-3'
                    fontSize='sm'
                    fontWeight='bold'
                    color='white'
                    bgColor={context.colorScheme + `.900`}
                    p='2'
                    overflow='hidden'
                    maxH='20px'>{cart?.line_items?.length}</Circle>
                <Badge colorScheme={context.colorScheme} ml='6'>{cart?.subtotal?.formatted_with_code}</Badge>
            </Flex>
        </Flex>
    )
}
