import {
    Button, Box, Image, Text, Flex, Center, Tooltip, Divider, Heading,
} from '@chakra-ui/react'
import { DeleteIcon } from '@chakra-ui/icons'
import { useContext } from 'react'
import { CommerceContext } from '../../App'
import EmptySVG from '../../assets/undraw_empty_xct9.svg'
import Item from './item/Item'
import { Link } from 'react-router-dom'
import Loader from '../../components/utils/Loader'

export default function Cart({items}) {
    const context = useContext(CommerceContext);
    const isEmpty = () => (
        <Center pos='relative'>
            <Image src={EmptySVG} maxH='60vh'></Image>
        </Center>
    )
    if (!items.line_items) return <Loader />;
    return (
        <Flex flexDir={['column', 'column', 'column', 'row']}>
            <Box
                p='6'
                flex='1'>
                <Heading>Tw贸j koszyk</Heading>
                <Flex
                    justifyContent='space-between'
                    alignItems='center'
                    fontSize='sm'
                    fontWeight='normal'>
                    <Text>{ items.line_items.length } przed.</Text>
                    {items.line_items.length ? (
                        <Tooltip label='Wyczy koszyk' hasArrow>
                            <Center
                                onClick={()=>context.handleClearCart()}
                                cursor='pointer'
                                w='50px'
                                py='2'
                                fontSize='lg'>
                                <DeleteIcon />
                            </Center>
                        </Tooltip>
                    ) : null}
                </Flex>
                <Box>
                    {items.line_items.length === 0 ? isEmpty() : null}
                    {items.line_items.map(item => (
                        <Item item={item} key={item.id} />
                    ))}
                </Box>
            </Box>
            
            {/* right box / checkout box */}
            {items.line_items.length !== 0 && (
                <Box
                    p='6'
                    maxW={['100%', '100%', '100%', '400px']}
                    w='100%'
                    minH='200px'
                    bgColor={context.colorScheme + `.100`}>
                    <Flex flexDir='column' h='100%'>
                        <Box textTransform='uppercase' fontSize='sm' py='6'>
                            <Flex justifyContent='space-between'>
                                <Text>Cena za wszystko</Text>
                                <Text>{items.subtotal.formatted_with_code}</Text>
                            </Flex>
                        </Box>

                        <Divider borderColor='black' />
                        
                        <Flex
                            justifyContent='space-between'
                            textTransform='uppercase'
                            fontWeight='bold'
                            fontSize={['sm', 'lg']}
                            py='6'>
                            <Text>Razem (z VAT)</Text>
                            <Text ml='2' color='green'>{ items?.subtotal?.formatted_with_code }</Text>
                        </Flex>
                        <Center>
                            <Button
                                as={Link}
                                to='/checkout'
                                colorScheme={context.colorScheme}
                                >Zamawiam</Button>
                        </Center>
                    </Flex>
                </Box>
            )}
        </Flex>
    )
}
