import { Box, Button, Container, Divider, Flex, Heading, Image, Spacer, Text } from '@chakra-ui/react';
import React, { useContext, useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import { CommerceContext } from '../../App';
import { ShowGroupsIfExists } from '../../components/Product/Functions';
import Loader from '../../components/utils/Loader';
import { commerce } from '../../lib/commerce';

export default function Item() {
    const params = useParams('permalink');
    const permalink = params.permalink;

    const [data, setData] = useState({})
    const [variant, setVariant] = useState({})

    const context = useContext(CommerceContext)
    
    const getItemData = async () => {
        const data = await commerce.products.retrieve(permalink, {type: 'permalink'});
        setData(data);
    }

    useEffect(() => {
        getItemData();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])
    useEffect(() => {
        if (data.variant_groups && data.variant_groups.length > 0) {
            data.variant_groups.map(group => group.name === 'size' && setVariant(group.options[0]));
        }
    }, [data])

    if (Object.keys(data).length === 0) {
        return <Loader />
    }
    return (
        <Container
            as={Flex}
            flexDir={['column', 'column', 'row']}
            pos='relative'
            maxW='1200px'
            borderWidth='1px'
            my='6' p='6'
            justifyContent='space-between'>
            {data.is.sold_out && (
                <Flex
                    pos='absolute'
                    top='0'
                    left='0'
                    w='100%'
                    h='100%'
                    bgColor='whiteAlpha.600'
                    zIndex='1'
                    flexDir='column'
                    justifyContent='center'
                    alignItems='center'
                    userSelect='none'
                >
                    <Heading textTransform='uppercase'>Wyprzedane</Heading>
                    <Button
                        as={Link}
                        to='/'
                        mt='6'
                        variant='outline'
                        colorScheme={context.colorScheme}>
                        Powr贸t do strony g贸wnej
                    </Button>
                </Flex>
            )}
            <Flex
                justifyContent='center'
                w='100%'
                borderRadius='lg'>
                <Image
                    src={data.media.source}
                    w='100%'
                    maxW='400px'
                    h='400px'
                    objectPosition='center'
                    objectFit='contain'
                    borderRadius='lg'
                />
            </Flex>
            <Flex
                flexDir='column'
                maxW={['100%', '100%','50%','50%']}
                w='100%'
                mt={['6', '6','0','0']}
                ml={['0', '0','6','6']}
                p='2'>
                
                <Box>
                    <Heading fontSize='xl'>{ data.name }</Heading>
                    <Text my='2' dangerouslySetInnerHTML={{
                        __html: data.description
                    }} />

                    <Divider borderColor='black' />

                    <Heading
                        my='6'
                        fontSize='2xl'
                        color='green'>{data.price.formatted_with_code}</Heading>
                    
                    {ShowGroupsIfExists(data.variant_groups, variant, setVariant, context.colorScheme)}
                </Box>
                
                <Spacer />

                <Box>
                    <Button
                        onClick={() => context.checkBeforeAddingToCart(data.id, data.variant_groups, variant, data.is.sold_out)}
                        w='100%'
                        mt='6'
                        isDisabled={data.is.sold_out}
                        colorScheme={context.colorScheme}>Dodaj do koszyka</Button>
                </Box>
            </Flex>
        </Container>
    )
}
