import { Box, Image, Circle, Tooltip, Text, Flex, Spacer } from '@chakra-ui/react';
import { AddIcon } from '@chakra-ui/icons'
import { CommerceContext } from '../../App';
import { useState, useEffect, useContext } from 'react';
import { Link } from 'react-router-dom';
import { ShowGroupsIfExists } from './Functions';

export default function Product({product}) {
	const context = useContext(CommerceContext)
	const [variant, setVariant] = useState({});

	useEffect(() => {
		if (product.variant_groups.length > 0) {
			product.variant_groups.map(group => group.name && setVariant(group.options[0]));
		}
	}, [product.variant_groups])

    return (
        <Box
            m='5'
			maxW="300px"
			w='100%'
			minH='400px'
			borderRadius="lg"
			borderWidth='1px'
			overflow="hidden"
			userSelect='none'
			position='relative'>
			<Box h='300px' p='2' pos='relative' overflow='hidden'>
				<Image
					src={product.media.source}
					alt={product.name}
					w='100%'
					h='100%'
					borderRadius='lg'
					objectFit='cover'
				/>
				{ product.is.sold_out && (
					<Flex
						w='100%'
						h='100%'
						borderRadius='lg'
						pos='absolute'
						top='0'
						left='0'
						justifyContent='center'
						alignItems='center'
						bgColor='rgba(255,255,255,.6)' >
						<Text
							fontWeight='bold'
							fontSize='lg'>
							Wyprzedane
						</Text>
					</Flex>
				)}
			</Box>
			<Flex
				flexDir='column'
				px="6"
				h='calc(100% - 300px)'>
				<Box
					as={Link}
					to={`/przedmiot/` + product.permalink}
					fontWeight="semibold"
					fontSize='xl'
					isTruncated>
					{product.name}
				</Box>
				<Flex
					flexDir='column'
					h='100%'
					justifyContent='space-between'>

					<Text
						fontSize='sm'
						textColor='gray.500'
						dangerouslySetInnerHTML={{ __html: product.description }} />

						{ShowGroupsIfExists(product.variant_groups, variant, setVariant, context.colorScheme)}

					<Spacer />

					<Flex justifyContent='space-between' alignItems='center'>
						<Text
							fontSize='lg'
							fontWeight='bold'
							my='5'>
							{product.price.formatted_with_code}
						</Text>
						<Box
							_active={{
                                transform: 'scale(0.9)',
                            }}
							cursor={product.is.sold_out ? 'not-allowed' : 'pointer'}
							onClick={() => context.checkBeforeAddingToCart(product.id, product.variant_groups, variant, product.is.sold_out)}>
							<Tooltip label='Dodaj do koszyka'>
								<Circle backgroundColor='gray.300' p='2'>
									<AddIcon />
								</Circle>
							</Tooltip>
						</Box>
					</Flex>
				</Flex>
			</Flex>
		</Box>
    )
}
