import React, { useState, useEffect } from 'react'
import { useToast, SimpleGrid } from '@chakra-ui/react';
import Product from './components/Product/Product';
import Navbar from './components/Navbar/Navbar';
import Checkout from './views/checkout/Checkout';
import Cart from './views/cart/Cart';
import { commerce } from './lib/commerce'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import Item from './views/item/Item';
import Loader from './components/utils/Loader';

const COLOR_SCHEME = 'blue';

export const CommerceContext = React.createContext({
  handleUpdateItem: () => { },
  handleClearCart: () => { },
  checkBeforeAddingToCart: () => {},
  colorScheme: ''
});

export default function App() {
  const [products, setProducts] = useState([]);
  const [cart, setCart] = useState({});
  const toast = useToast();
  const [order, setOrder] = useState({});

  const fetchProducts = async () => {
    try {
      const { data } = await commerce.products.list();
      setProducts(data);
    } catch (error) {
      toast({
        title: "Nie mo偶na pobra danych sklepu.",
        description: "Twoje poczenie mo偶e by niestabilne.",
        status: "error",
      })
    }
  }

  const fetchCart = async () => {
    try {
      const res = await commerce.cart.retrieve();
      setCart(res);
    } catch (error) {
      toast({
        title: "Nie mo偶na pobra danych koszyka.",
        description: "Twoje poczenie mo偶e by niestabilne.",
        status: "error",
      })
    }
  }
  const refreshCart = async () => {
    const newCart = await commerce.cart.refresh();

    setCart(newCart);
  }

  const handleAddToCart = async (productId, quantity, variant) => {
    try {
      const item = await commerce.cart.add(productId, quantity, variant);
      setCart(item.cart);
      toast({
        title: "Dodano przedmiot do koszyka.",
        description: "Sprawd藕 sw贸j koszyk.",
        status: "success",
        duration: 9000,
        isClosable: true,
      })
    } catch (error) {
      toast({
        title: "Wystpi nieoczekiwany bd.",
        description: "Nie udao si doda przedmiotu.",
        status: "error",
        duration: 9000,
        isClosable: true,
      })
    }
  }
  const checkBeforeAddingToCart = (productId, variantGroups, variant, isSoldOut = false) => {
		if (isSoldOut) {
			return false;
		}
		if (variantGroups.length === 0) {
			handleAddToCart(productId, 1);
		} else {
			variantGroups.map(group => {
        const variantInfo = {
          [group.id]: variant.id
        }
        handleAddToCart(productId, 1, variantInfo)
        return true;
			});
		}
	}
  
  const handleClearCart = async () => {
    const item = await commerce.cart.empty();
    setCart(item.cart);
    toast({
      title: "Wyczyszczono koszyk.",
      description: "Tw贸j koszyk jest od teraz pusty ",
      status: "success",
      duration: 9000,
      isClosable: true,
    })
  }

  const handleCaptureCheckout = async (checkoutTokenID, newOrder) => {
    try {
      const incomingOrder = await commerce.checkout.capture(checkoutTokenID, newOrder);

      setOrder(incomingOrder);
      refreshCart();
      return true;
    } catch (error) {
      toast({
        title: "Nie mo偶emy przeanalizowa Twojego zam贸wienia.",
        description: "Wystpi problem z zakupem przedmiotu. Spr贸buj ponownie p贸藕niej.",
        status: "error",
        duration: 9000,
        isClosable: true,
      });
      return false;
    }
  }

  const handleUpdateItem = async (id, quantity) => {
    const item = await commerce.cart.update(id, {quantity});
    setCart(item.cart);
  }

  useEffect(() => {
    fetchProducts();
    fetchCart();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  if (!cart) return 'loading...';

  return (
    <CommerceContext.Provider
      value={{
        handleUpdateItem,
        handleClearCart,
        colorScheme: COLOR_SCHEME,
        checkBeforeAddingToCart
      }}>
      <Router>
        <Navbar cart={cart}
          onClearCart={handleClearCart}
        />
        <Switch>
          <Route exact path='/'>
            <SimpleGrid spacing='5' mt='5' minChildWidth='300px' justifyItems='center'>
              {Object.keys(cart).length > 0 ? (
                <>
                  { products.map(product => (
                    product.is.active && (
                      <Product
                        product={product}
                        key={product.id}
                        onAddToCart={handleAddToCart} />
                    )
                  )) }
                </>
              ) : (
                <Loader />
              )}
            </SimpleGrid>
          </Route>
          <Route exact path='/cart'><Cart items={cart} /></Route>
          <Route exact path='/checkout'>
            <Checkout
              order={order}
              onCaptureCheckout={handleCaptureCheckout}
              cart={cart} />
          </Route>
          <Route exact path='/przedmiot/:permalink'>
              <Item />
          </Route>
        </Switch>
      </Router>
    </CommerceContext.Provider>
  )
}
