import { Box, Divider, Flex, Heading, Text } from '@chakra-ui/react'
import React from 'react'

export default function Review({checkoutToken}) {
    return (
        <Box>
            <Heading fontSize='xl' my='6'>Podsumowanie zam贸wienia</Heading>
            {checkoutToken.live.line_items.map(item => (
                <Flex
                    key={item.id}
                    my='4'
                    justifyContent='space-between'>
                    <Box>
                        <Text fontWeight='bold'>{item.name}</Text>
                        {item.selected_options.length > 0 && item.selected_options.map(option => (
                            <Text fontWeight='normal' fontSize='md' key={option.group_id}>
                                {option.group_name === 'size' && 'Rozmiar: '}
                                {option.group_name === 'color' && 'Kolor: '}
                                {item.selected_options.map(opt => opt.option_name)}
                            </Text>
                        ))}
                    </Box>
                    <Text>{item.quantity + ` x ` + item.price.formatted_with_code}</Text>
                    
                </Flex>
            ))}
            <Divider />
            <Flex fontWeight='bold' justifyContent='space-between' my='4'>
                <Text>Cena za wszystko:</Text>
                <Text color='green'>{checkoutToken.live.subtotal.formatted_with_code}</Text>
            </Flex>
        </Box>
    )
}
