import { Circle, Flex, Text } from "@chakra-ui/react"

export const ShowGroupsIfExists = (variantGroups, variant, setVariant, colorScheme) => {
    if (variantGroups) {
        return variantGroups.map(group => (
            <Flex key={group.id} my='2'>
                <Text>
                    {group.name === 'size' && 'Rozmiary:'}
                    {group.name === 'colors' && 'Kolory:'}
                </Text>
                {group.options.map(size => (
                    <Circle
                        minw='25px'
                        h='25px'
                        px='1'
                        mx='2'
                        bgColor={variant.id === size.id ? colorScheme + `.100` : colorScheme + `.50`}
                        fontSize='xs'
                        cursor='pointer'
                        onClick={()=>setVariant(size)}
                        key={size.id}>
                            {size.name}
                    </Circle>
                ))}
            </Flex>
        ))
    }
    return null;
}