import React, { useContext, useEffect, useState } from 'react'
import {
    Box, Center, CircularProgress, CircularProgressLabel,
    Container, Divider, Heading, Text, Flex, FormControl,
    FormLabel, Input, FormHelperText, Button, Spacer, useToast
} from '@chakra-ui/react'
import { useForm, FormProvider } from 'react-hook-form';
import { CommerceContext } from '../../App';
import { commerce } from '../../lib/commerce'
import { Link } from 'react-router-dom';
import Payment from './Payment';
import Confirmation from './Confirmation';

const CustomInput = ({id, label, inputType = 'text', placeholder, helperText , register, isRequired}) => {
    return (
        <FormControl id={id} my='5' isRequired={isRequired}>
            <FormLabel>{ label }</FormLabel>
            <Input type={inputType} placeholder={placeholder} {...register(id)} />
            <FormHelperText>{ helperText }</FormHelperText>
        </FormControl>
    )
}

export default function Checkout({cart, onCaptureCheckout}) {
    const [step, setStep] = useState(1);
    const [checkoutToken, setCheckoutToken] = useState(null);
    const [shippingData, setShippingData] = useState({});
    const toast = useToast();

    const { register, handleSubmit } = useForm();
    const onSubmit = data => console.log(data);
    const context = useContext(CommerceContext);
    
    useEffect(() => {
        const generateToken = async () => {
            try {
                const token = await commerce.checkout.generateToken(cart.id, {type: 'cart'});

                setCheckoutToken(token);
            } catch (error) {
                toast({
                    title: "Wystpi bd.",
                    description: "Nie mo偶emy wczyta ID zam贸wienia. Spr贸buj ponownie p贸藕niej.",
                    status: "error",
                    duration: 9000,
                    isClosable: true,
                });
            }
        }
        generateToken();
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);
    const nextStep = (data) => {
        data.preventDefault();

        setShippingData({
            firstname: data.target[0].value,
            lastname: data.target[1].value,
            email: data.target[2].value,
        });

        setStep(2);
    }
    const ClientInfo = () => (
        <Box as='form' onSubmit={(e) => nextStep(e)}>
            <CustomInput
                id='firstname'
                label='Imi'
                placeholder='Jan'
                isRequired
                register={register} />
            <CustomInput
                id='lastname'
                label='Nazwisko'
                placeholder='Kowalski'
                isRequired
                register={register} />
            <CustomInput
                id='email'
                label='E-mail'
                type='email'
                placeholder='jan_kowalski@example.com'
                helperText='Nigdy nie udostpniamy twojego adresu e-mail.'
                isRequired
                register={register} />
            {/* <CustomInput
                id='tel'
                type='tel'
                label='Telefon'
                placeholder='123 456 789'
                isRequired
                register={register} /> */}
            
            <Flex>
                <Button
                    as={Link}
                    to='/cart'
                    colorScheme={context.colorScheme}
                    variant='outline'
                    my='5'>Wr贸 do koszyka</Button>
                <Spacer />
                <Button
                    colorScheme={context.colorScheme}
                    my='5'
                    type='submit'>Dalej</Button>
            </Flex>
        </Box>
    )
    const handleCheckValue = () => {
        if (step === 1) return '0';
        if (step === 2) return '50';
        if (step === 3) return '100';
        return 0;
    }

    return (
        <Container
            border='1px'
            borderRadius='lg'
            my='6'
            minH='400px'>
            <Heading fontSize='3xl' py='6'>
                <Center>Checkout</Center>
            </Heading>

            <Divider />

            {/* top bar */}
            <Flex
                py='2'
                alignItems='center'
                justifyContent='space-between'>
                <CircularProgress value={handleCheckValue()} thickness='5px' size='50px'>
                    <CircularProgressLabel>{handleCheckValue()}%</CircularProgressLabel>
                </CircularProgress>
                <Text fontSize='xl' ml='6' flex='1'>
                    {step === 1 && 'Dane kontaktowe'}
                    {step === 2 && 'Patno'}
                    {step === 3 && 'Potwierdzenie zam贸wienia'}
                </Text>
            </Flex>

            <Divider />

            <FormProvider>
                {step === 1 &&
                    <ClientInfo setStep={setStep} />}
                {step === 2 &&
                    <Payment
                        onCaptureCheckout={onCaptureCheckout}
                        shippingData={shippingData}
                        checkoutToken={checkoutToken}
                        onSubmit={onSubmit}
                        setStep={setStep}
                        handleSubmit={handleSubmit} />}
                {step === 3 &&
                    <Confirmation />
                }
            </FormProvider>

        </Container>
    )
}
