import React, { useContext } from 'react'
import { Box, Flex, Button, Spacer, Heading, Divider, useToast } from '@chakra-ui/react'
import { CommerceContext } from '../../App'
import Review from './Review'
import { Elements, ElementsConsumer, CardElement } from '@stripe/react-stripe-js'
import { loadStripe } from '@stripe/stripe-js'
import { useState } from 'react'

const stripePromise = loadStripe(process.env.REACT_APP_STRIPE_PK);

export default function Payment({setStep, shippingData, checkoutToken, onCaptureCheckout}) {
    const context = useContext(CommerceContext)
    const [isLoading, setLoading] = useState(false);
    const toast = useToast();

    const handleSubmit = async (event, elements, stripe) => {
        event.preventDefault();

        if (!stripe || !elements) return;

        const cardElement = elements.getElement(CardElement);

        const { error, paymentMethod } = await stripe.createPaymentMethod({
            type: 'card',
            card: cardElement
        });

        if (error) {
            return toast({
                title: "Wystpi bd.",
                description: error.message,
                status: "error",
                duration: 9000,
                isClosable: true,
            })
        } else {
            setLoading(true);
            const orderData = {
                line_items: checkoutToken.live.line_items,
                customer: {
                    firstname: shippingData.firstname,
                    lastname: shippingData.lastname,
                    email: shippingData.email,
                },
                fulfillment: { shipping_method: shippingData.shippingOption },
                payment: {
                    gateway: 'stripe',
                    stripe: {
                        payment_method_id: paymentMethod.id
                    }
                }
            }
            await onCaptureCheckout(checkoutToken.id, orderData);
            setLoading(false);
            setStep(3);
        }
    }

    return (
        <Box>
            <Review checkoutToken={checkoutToken} />
            
            {console.log(checkoutToken.live.line_items)}
            <Divider />
            <Box>
                <Heading my='6' fontSize='xl'>Metoda patnoci</Heading>
                <Elements stripe={stripePromise}>
                    <ElementsConsumer>
                        {({elements, stripe}) => (
                            <form onSubmit={(e) => handleSubmit(e, elements, stripe)}>
                                <CardElement />
                                <br /><br />
                                <Flex>
                                    <Button colorScheme={context.colorScheme} variant='outline' my='5'
                                        onClick={()=>setStep(1)}>Wr贸</Button>
                                    <Spacer />
                                    <Button
                                        type='submit'
                                        isDisabled={!stripe}
                                        colorScheme={context.colorScheme}
                                        isLoading={isLoading}
                                        my='5'>
                                        Zapa ({checkoutToken.live.subtotal.formatted_with_code})
                                    </Button>
                                </Flex>
                            </form>
                        )}
                    </ElementsConsumer>
                </Elements>
            </Box>
            

            
        </Box>
    )
}
